.card {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 300px;
    background-color: aliceblue;
    background-color: rgb(255 255 255 / 0.1);
    backdrop-filter: blur(10px);
}

.card-img {
    width: 80%;
    height: 70%;
    cursor: pointer;
   
}


img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    box-shadow: 3px 3px 10px 3px rgb(104, 93, 73);
    /* Тінь */
    border: 2px solid grey;
}
.card-img:hover{
    animation: img-grow 5s ;
}

@keyframes img-grow {
   to {
        width: 90%;
    }
}