* {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  box-sizing: border-box;
}


body{
  display: flex;
  justify-content: center;
  margin: 0;
  width: 100%;
  font-size: 16px;
  background-color:rgb(41, 38, 38);
}
#root{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
body,
#root {
  width: 100%;
  height: 100%;
}
.portfolio{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-top:150px;
  position: relative;
}
.box-360{
  color: white;
  padding-top:40px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  position: absolute;
  display: none;
}
.box-360::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 1); /* Задайте кольоровий фон і прозорість */
  z-index: 1; 
  pointer-events: none; 
}
.panorama{
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-self: center;
  z-index:4;
}
.active-box-360{
  display: flex;
}
.btn-360{
  width: 100px;
  height: 30px;
  margin: 10px;
  cursor: pointer;
}


